import React from 'react'
import { Link } from "react-router-dom";

interface Props {
  children: React.ReactNode,
  linkType: string,
  url: string
}

const LinkWrapper = ({children, linkType, url}: Props) => {

  if (linkType === 'external') {
    return (
      <a 
        href={url}
        rel="noopener noreferrer"
        target="_blank"
        className={"card_image_link"}
      >
        {children}
      </a>
    )
  }

  if (linkType === 'local') {
    return (
      <Link to={"/" + url}>
        {children}
      </Link>
    )
  }

  return (
    <>
      {children}
    </>
  )
}

export default LinkWrapper
