import * as React from 'react'
import LinkWrapper from '../linkWrapper/LinkWrapper'
import ghIcon from '../../img/GitHub-Mark-32px.png';
import data from '../../data'
import "../../App.css"

interface Props {
  isHome: boolean
}

function Home({isHome}: Props) {
  console.log('render Home')
  return (
		<div className={`home ${isHome ? 'show' : ''}`}>
      <a
        className="gh_link"
        href="https://github.com/callumgrayson"
        rel="noopener noreferrer"
      >
        <img src={ghIcon} alt="GitHub icon link" />
      </a>

      <div className="container">
        <div className="width-fixer">
          <div className="head_area">
            <h1>Callum Grayson - Developer</h1>
            <div className="intro">
              <h4>
                This is a gallery of interfaces I have built for learning web development technologies. Some are original work. Some are from tutorials or courses. Some are adapted from code challenges.
              </h4>
            </div>
          </div>

          <div className="App">

            <div>
              {data.map((el) => (
                <div className="card_space">
                  <h2>{el.year}</h2>
                  {/* <div className={"cards"}> */}
                  {el.cards.map((card) => (
                    <div className="card">
                      <LinkWrapper linkType={card.linkType} url={card.url}>
                        <h4 className="card_title">
                          {card.title}
                        </h4>
                        {card?.origin === 'original' ? <div className="card_origin_badge">{card.origin}</div> : null}
                        <div
                          className="card_image_box"
                        >
                          {card.imgSrc ? <img
                            className="card_image"
                            src={card.imgSrc}
                            alt={`Screenshot of the ${card.title} app`}
                          /> : null}
                        </div>
                      </LinkWrapper>
                      {/* <a
                        href={card.url ? `https://callumgrayson.github.io/${card.url}` : ''}
                        rel="noopener noreferrer"
                        className={"card_image_link"}
                      >
                      </a> */}
                      <p className="card_caption">
                        {card.displayText}
                      </p>
                    </div>
                  ))}
                  {/* </div> */}
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>

      <div className="bottom_area" />
      <div className="face_spacer" />
    </div>
  );
}

export default Home
