import * as React from "react";
import { Routes, Route, Link, useLocation } from "react-router-dom";
import Anagrams from './components/anagrams/Anagrams'
import Home from './components/Home/Home'
import {ReactComponent as LeftArrow} from './svg/left-direction-arrow.svg'
import './App.css';

function App() {
  let location = useLocation()
  let isHome = location.pathname === '/'
  return (
        <div className={`app ${isHome ? "" : "no-face"}`}>
          {/* <div style={{backgroundColor: 'white', minHeight: '100vh'}}> */}
            {console.log(`location`, location)}
            <Home isHome={isHome}/>
            {isHome ? null : (
              <Link to="/" className="link_to_home">
                <div className="back-button">
                  <LeftArrow />  
                </div>
              </Link>
            )}

            <Routes>
              {/* <Route path="/" element={home} />  */}
              <Route path="anagrams" element={<Anagrams />} />
            </Routes>
          {/* </div> */}
        </div>
      );
	
}

export default App;





